.Koło {
  align-items: center;
  animation: rotate 40s infinite linear;
  z-index: 3;
}

.Magia {
  animation: color 40s infinite;
  align-items: center;
  display: flex;
  flex-direction: column;
  background-color: rgb(171, 164, 66);
  height: 110vh;
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes color {
  0% {
    background-image: rgb(171, 164, 66);
  }
  6.25% {
    background-color: rgb(212, 203, 77);
  }
  11.5% {
    background-color: rgb(235, 224, 76);
  }
  12.5% {
