@keyframes animateBg {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 100% 0;
  }
}
@media only screen and (min-width: 600px) {
  .Koło {
    max-width: 100vw;
  }
}
@media only screen and (max-width: 600px) {
  .Koło {
    max-width: 70vw;
  }
}
